.header {
  padding: 30px 0 10px;
  background: rgb(245, 245, 245);
  &__logo {
    img {
      height: 45px; } }
  a {
    font-size: 16px;
    font-weight: 600;
    line-height: 1.25;
    color: $dark-two;
    @include center-y;
    justify-content: flex-end;
    &:hover {
      color: #ed135d; }
    img {
      margin-right: 15px; } } }
